.ipwrap{
position:relative;
margin-top:35px;
padding:5px;
height:30px;

}
.iplabel{
display: block;
position: absolute;
z-index: 10;
top: 3px;
left: 7px;

transition: all 0.4s;
}
.ip{
width: 100%;
position: absolute;
top: 0;
left: 0;
background: transparent;
padding: 5px;
z-index: 11;
border-top: none;
border-bottom-width: 2px;
outline: none;
transition: all 0.4s;
border-left: none;
border-right: none;
}
.ip:focus +.iplabel{
top:-20px;
font-size: 17px;
font-weight: 600;
}
.ip:not(:placeholder-shown) +.iplabel{
top:-20px;
font-size: 17px;
font-weight: 600;
}
.ip:not(:valid) {
border-color: red;
}


